import Axios from "axios";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { getNetto } from "../../actions/LogicAction";
import { showModal } from "../../actions/ModalAction";
import { gagal } from "../../Alert";
import { getToday } from "../../getDate";
import { renderField, renderFieldSelect } from "../../renderField";

const server = process.env.REACT_APP_BACKEND_URL;
const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});

const maptoState = (state) => {
  return {
    sumNetto: state.perhitungan.sumNetto,
  };
};
class FormHeadPenjualanLM extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
    };
  }
  pilihSupplier = (input) => (e) => {
    var supp = e.target.value ? e.target.value : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
    localStorage.setItem("namaSupplier", result_explode[0]);
    localStorage.setItem("kodeSupplier", result_explode[1]);
  };
  componentDidMount() {
    Axios.get(server + "customers")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch((err) => gagal("Mohon periksa koneksi anda"));
    this.props.dispatch(getNetto("TransaksiPenjualanLM"));
    this.props.change("totalNW", this.props.sumNetto);
    this.props.change("tanggal_invoice", getToday);
    this.props.change("tanggalBarang", getToday);
  }
  setCustomer(e) {
    let hasil = e || "DEFAULT|DEFAULT";
    let split = hasil.split("|");
    this.props.change("kodeCustomer", split[0]);
    this.props.change("namaToko", split[1]);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="col-lg-12">
          <div className="row">
            <div className="col-lg-4 col-md-4">
              <Field
                label="Customer"
                name="customer"
                id="kodeCustomer"
                className="form-control"
                component={renderFieldSelect}
                onChange={(e) => this.setCustomer(e)}
                options={this.state.listSupplier.map((kode) => {
                  let data = {
                    value: kode.kodeCustomer + "|" + kode.namaToko,
                    name: kode.namaToko,
                  };
                  return data;
                })}
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                type="date"
                label="Tanggal invoice"
                name="tanggal_invoice"
                component={renderField}
              />
            </div>
            <div className="col-md-4 col-lg-4 d-none">
              <Field
                type="text"
                label="Tanggal invoice"
                name="kodeCustomer"
                component={renderField}
              />
            </div>
            <div className="col-md-4 col-lg-4 d-none">
              <Field
                type="text"
                label="Tanggal invoice"
                name="namaToko"
                component={renderField}
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                type="date"
                label="Tanggal Barang"
                name="tanggalBarang"
                component={renderField}
              />
            </div>
            <div className="col-md-3 col-lg-3 mt-3">
              <Field
                type="text"
                label="Keterangan"
                name="keterangan"
                component={renderField}
              />
            </div>
            <div className="col-md-3 col-lg-3 mt-3">
              <Field
                type="text"
                label="Nomor bon"
                name="noBon"
                component={renderField}
              />
            </div>
            <div className="col-md-3 col-lg-3 mt-3">
              <label htmlFor="">Ongkos (Rp)</label>
              <Field
                type="telp"
                label="Ongkos (Rp)"
                name="ongkos"
                className="form-control"
                component="input"
                {...currencyMask}
              />
            </div>
            <div className="col-lg-3 mt-3">
              <label htmlFor="">Total NW Ext</label>
              <Field
                type="number"
                label="Total NW Ext"
                name="totalNW"
                className="form-control"
                component="input"
                onBlur={this.props.change("totalNW", this.props.sumNetto)}
              />
            </div>
            <div className="col-md-12 col-lg-12 mt-3">
              <div className="row">
                <div className="col-lg-6 col-md-6">
                  <div className="text-left">
                    <button
                      className="btn btn-primary"
                      type="submit"
                      disabled={this.props.onSend}
                      data-tut="reactour_3"
                    >
                      {this.props.onSend ? (
                        <span
                          className="spinner-border spinner-border-sm mr-3"
                          aria-hidden="true"
                        ></span>
                      ) : null}
                      Simpan
                      <i className="fa fa-paper-plane  ml-3"></i>
                    </button>
                  </div>
                </div>
                <div className="col-lg-6 col-md-6">
                  <div className="text-right">
                    <button
                      type="button"
                      className="btn btn-primary"
                      onClick={() => this.props.dispatch(showModal())}
                      data-tut="reactour_1"
                    >
                      Tambah Data
                      <i className="fa fa-plus ml-3"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormHeadPenjualanLM = reduxForm({
  form: "FormHeadPenjualanLM",
  enableReinitialize: true,
})(FormHeadPenjualanLM);
export default connect(maptoState, null)(FormHeadPenjualanLM);
